import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Normalization from '@/views/NormalizationSections'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/norm_section',
    name: 'NormalizationSections',
    component: Normalization
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
